  opacity: 0;
  position: absolute;
}
input[type="checkbox"]:hover ~ .checkbox-box, input[type="checkbox"]:focus ~ .checkbox-box, input[type="checkbox"]:checked ~ .checkbox-box{
  border-color: #5044ff;
}
